.App {
  position: relative;
}

.inner {
  position: absolute;
  top: 70%;
  left: 50%;
  transform: translate(-50%, 25%);
  width: fit-content;
  border: black solid 3px;
  padding: 10px;
  border-radius: 10px;
  background-color: rgba(0, 0, 0, 0.644);
}

.options {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.option {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
  height: 50px;
  margin: 10px 0;
  border: rgba(0, 4, 255, 0.541) solid 3px;
  border-radius: 5px;
  cursor: pointer;
}

.slide {
  width: 100%;
  text-align: center;
}

.password {
  text-align: center;
}

.generate {
  text-align: center;
}

h1 {
  color: white;
}

p {
  color: white;
}

button {
  background-color: rgba(0, 4, 255, 0.541);
  border: rgba(0, 4, 255, 0.541) solid 3px;
  border-radius: 5px;
  color: white;
  font-size: large;
  font-weight: bolder;
}